.button {
  display: inline-block;
  background-color: rgb(50, 50, 50);
  border-radius: 10px;
  padding: 10px 50px;
  min-width: 130px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.15s ease-in-out, border-color 0.15s ease-in-out;
  border: 1px solid transparent;
}
.button,
.button span {
  color: #fff;
}
.button i,
.button span,
.button path,
.button svg {
  transition: all 0.15s ease-in-out;
}
.button:hover {
  background-color: #111111;
}
.button:active {
  background-color: black;
  transform: translateY(1px);
}
.button--circle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  min-width: 32px;
  padding: 0;
  border-width: 2px;
  border-radius: 50%;
}
.button .go-back-btn {
  color: #ffffff;
}
.button--black {
  background-color: #232323;
}
.button--black:hover, .button--black:active {
  background-color: #3d3d3d;
}
.button--outline {
  background: linear-gradient(135deg, rgb(255, 255, 255), rgb(95, 95, 95) 100%);
  border: 2px solid #231717;
  transition: all 0.3s;
  background-size: 200% 100%;
  background-position: 100% 0;
  transition: background-position 1s;
  border: 0;
}
.button--outline:hover {
  background: linear-gradient(315deg, rgb(255, 255, 255), rgb(95, 95, 95) 100%);
  background-position: 0 0;
  color: #545454;
}
.button--outline,
.button--outline span {
  transition: all 0.3s;
}
.button--outline:hover,
.button--outline span:hover {
  color: #353535;
  transition: all 0.3s;
}
.button--outline svg path {
  fill: #ffffff;
}
.button--outline:hover,
.button--outline:hover span {
  color: #353535;
}
.button--outline:hover svg path {
  fill: #3c3c3c;
}
.button--outline:active {
  background-color: #bd00b1;
}
.button__delimiter {
  width: 1px;
  height: 25px;
  background-color: rgba(255, 255, 255, 0.25);
  margin-left: 14px;
  margin-right: 14px;
}
.button--add {
  display: inline-block;
  white-space: nowrap;
  padding-left: 5px;
  padding-right: 5px;
  border-radius: 7px;
}
.button--add svg {
  margin-right: 10px;
}
.button--add span {
  font-weight: 600;
  font-size: 16px;
}
.button--add:hover i {
  background-color: #4c4c4c;
  color: #000000;
}
.button--add i {
  display: inline-block;
  border-radius: 30px;
  background-color: #000000;
  color: #fff;
  font-weight: 600;
  width: 22px;
  height: 22px;
  font-style: normal;
  font-size: 13px;
  line-height: 22px;
  position: relative;
  top: -1px;
  left: 3px;
}
.button--cart {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  max-width: 80px;
  min-width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 2px solid #fff;
}
.button--cart svg {
  margin-right: 8px;
  margin-bottom: 1px;
  font-size: 20px;
  width: 21px;
  height: 21px;
}
.button--cart span {
  font-weight: 600;
  font-size: 16px;
}

