}
.sort__popup ul li.active {
  font-weight: bold;
  background: rgba(255, 255, 255, 0.1921568627);
  color: #ffffff;
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loading-spinner {
